@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.centered-screen {
  max-width: 70vw;
  margin: auto;
}

@media (max-width: 1440px) {
  .getStarted-title {
    font-size: 1.4rem !important;
    line-height: 50px !important;
  }
}
@media (max-width: 1024px) {
  .centered-screen {
    max-width: 90vw;
  }
  .getStarted {
    flex-direction: column-reverse;
  }
  .getStarted-right-side {
    display: flex;
    justify-content: center;
  }
  .getStarted-right-side img {
    transform: translateX(0px) !important;
  }
  .getStarted-left-side {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .getStarted-title {
    font-size: 1.1rem !important;
    line-height: 40px !important;
  }
  .shortener {
    background-image: url(/static/media/bg-shorten-mobile.41c34b33.svg) !important;
    background-position: 100% 100% !important;
  }
  .submit-form {
    flex-direction: column;
  }
  .submit-form input {
    margin-right: 0 !important;
    margin-bottom: 15px;
  }
  .submit-form button {
    width: 100% !important;
  }
  .urls-list-item {
    flex-direction: column;
  }
  .item-right-side,
  .item-left-side {
    line-height: 40px;
    flex-direction: column;
    margin: 0 !important;
    align-items: center;
  }
  #button.shortened-button {
    width: 100%;
    margin: 0 !important;
  }
  .Banner {
    background-image: url(/static/media/bg-boost-mobile.4b04bd89.svg) !important;
  }
  .statistics-cards {
    grid-template-columns: 1fr !important;
    text-align: center;
  }
  .statistics-cards::after {
    display: none;
  }
  .Footer {
    grid-template-columns: 1fr !important;
  }
  .Footer .col {
    text-align: center;
    margin-bottom: 20px;
  }
  /* MOBILE NAVBAR STYLING */

  .nav-left-side,
  .nav-right-side #button {
    display: none !important;
  }
  #hamburger.active,
  #close-icon.active {
    display: flex !important;
    cursor: pointer;
  }
  .nav-mobile.active {
    display: block !important;
  }
  .nav-mobile.active {
    border-radius: 10px;
    color: white;
    font-weight: 500;
    text-align: center;
    font-size: 1.3rem;
    background: hsl(258, 26%, 31%);
    font-family: "Poppins", sans-serif;
  }
  .nav-group {
    padding: 20px 0;
    border-bottom: 1px solid hsl(0, 0%, 94%);
  }
  .nav-group-item {
    cursor: pointer;
    padding: 10px 0;
  }
  .nav-group-item:hover {
    background: hsl(259, 27%, 34%);
  }
  .nav-mobile-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
  }
  .nav-mobile-buttons #button.login {
    background: none;
    color: white;
  }
  .nav-mobile-buttons #button.login:hover {
    background: white;
  }
  .nav-mobile-buttons #button {
    font-family: "Poppins", sans-serif;
    margin: 5px 0;
    font-weight: 700;
    font-size: 1.1rem;
    width: 90% !important;
  }
}
@media (max-width: 425px) {
  .getStarted-right-side img {
    min-width: 150px !important;
  }
  .banner-title {
    font-size: 1.4rem;
  }
}

.Banner {
  background: hsl(257, 27%, 26%);
  padding: 40px 0px;
  display: grid;
  place-items: center;
  background-image: url(/static/media/bg-shorten-desktop.0d9742f4.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.banner-title {
  color: white;
  font-family: "Poppins", sans-serif;
}
#button.banner-button {
  margin-top: 10px;
  width: 200px;
  padding: 12px;
}

.Footer {
  background: hsl(255, 11%, 22%);
  font-family: "Poppins", sans-serif;
  display: grid;
  place-items: center;
  padding: 50px 0;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.col {
  color: white;
}

.col strong {
  font-size: 1.3rem;
}

.col ul {
  margin-top: 10px;
  font-size: 0.9rem;
}
.col ul li {
  padding: 1px 0;
  margin: 5px 0;
  color: hsl(0, 0%, 80%);
  display: block;
  cursor: pointer;
  transition: all 0.35s;
}
.col ul li:hover {
  color: hsl(180, 66%, 49%);
}
.col.four ul {
  display: flex;
  align-items: center;
}
.col.four ul li {
  margin: 10px;
}
.col.four ul li img:hover {
  -webkit-filter: invert(10%) sepia(54%) saturate(800%) hue-rotate(86deg)
    brightness(118%) contrast(119%);
          filter: invert(10%) sepia(54%) saturate(800%) hue-rotate(86deg)
    brightness(118%) contrast(119%);
}

.getStarted {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Poppins", sans-serif;
}
.getStarted-left-side {
  max-width: 500px;
  width: 100%;
}
.getStarted-title {
  font-size: 2rem;
  line-height: 70px;
  color: hsl(0, 0%, 30%);
}
.getStarted-desc {
  line-height: 23px;
  letter-spacing: 1px;
  color: hsl(0, 0%, 35%);
}
.getStarted-right-side img {
  width: 95%;
  min-width: 350px;
  max-width: 600px;
  transform: translateX(150px);
}
#button.get-started {
  margin-top: 15px;
}

/* HEADER COMPONENT STYLES */
/* ------------------------------------------------------- */

.nav {
  display: flex;
  height: 80px;
  align-items: center;
  justify-content: space-between;
}
.nav-left-side {
  display: flex;
  align-items: center;
}
.nav-logo {
  margin-right: 10px;
}
.list-group-item {
  display: inline-block;
  margin: 0 1px;
  font-weight: 550;
  padding: 5px 15px;
  transition: all 0.3s;
  color: hsl(0, 0%, 60%);
  cursor: pointer;
}
.list-group-item:hover {
  color: hsl(0, 0%, 30%);
}

#hamburger,
#close-icon,
.nav-mobile,
.nav-mobile.deactive {
  display: none;
}

/* BUTTON STYLES */
/* ------------------------------------------------------- */

#button.login {
  background: white;
  color: hsl(0, 0%, 60%);
}
#button.login:hover {
  color: hsl(0, 0%, 40%);
}
#button.register {
  width: 100px;
}

#button {
  padding: 10px;
  width: 120px;
  background: hsl(180, 66%, 49%);
  border-radius: 999px;
  border: none;
  outline: none;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s;
}
#button.hover-effective:hover {
  background: rgba(22, 221, 221, 0.65);
}

/* SHORTENER STYLE */
.shortener-body {
  height: 100%;
  margin: 50px 0;
}
.shortener {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: hsl(257, 27%, 26%);
  border-radius: 10px;
  padding: 40px 0px;
  background-image: url(/static/media/bg-shorten-desktop.0d9742f4.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.shortener .submit-form {
  display: flex;
  align-items: center;
  max-width: 70%;
  margin: auto;
  position: relative;
}
.submit-form input {
  width: 100%;
  padding: 12px;
  border-radius: 5px;
  margin-right: 20px;
  outline: none;
  border: none;
  transition: all 0.25s;
}
.submit-form input::-webkit-input-placeholder {
  color: hsl(0, 0%, 75%);
}
.submit-form input:-ms-input-placeholder {
  color: hsl(0, 0%, 75%);
}
.submit-form input::placeholder {
  color: hsl(0, 0%, 75%);
}
.submit-form input:focus {
  transform: scale(1.03);
}
.submit-form input:focus::-webkit-input-placeholder {
  color: hsl(0, 0%, 40%);
}
.submit-form input:focus:-ms-input-placeholder {
  color: hsl(0, 0%, 40%);
}
.submit-form input:focus::placeholder {
  color: hsl(0, 0%, 40%);
}

#button.shortener-button {
  padding: 12px;
  width: 150px;
  border-radius: 2px;
}
#button.shortened-button.copied {
  background: hsl(257, 27%, 26%);
}

.form-error-message {
  color: hsl(0, 87%, 67%);
  position: absolute;
  bottom: -25px;
  font-family: "Poppins", sans-serif;
}
.input-err::-webkit-input-placeholder {
  color: hsl(0, 87%, 67%) !important;
  font-weight: 550;
}
.input-err:-ms-input-placeholder {
  color: hsl(0, 87%, 67%) !important;
  font-weight: 550;
}
.input-err::placeholder {
  color: hsl(0, 87%, 67%) !important;
  font-weight: 550;
}
.input-err {
  border: 1px solid hsl(0, 87%, 67%) !important;
  box-shadow: 0px 0px 6px 0.1px hsl(0, 87%, 67%);
}

/* SHORTENED ITEMS STYLES */

a {
  text-decoration: none;
  color: hsl(0, 0%, 20%);
  cursor: pointer;
}

.urls-list-item {
  display: flex;
  margin: 10px 0;
  padding: 12px;
  border-radius: 8px;
  background: rgba(238, 238, 238, 0.9);
  transition: all 0.25s ease-in-out;
}
.urls-list-item:hover {
  transform: scale(1.03);
  background: rgb(228, 228, 228);
}
.item-left-side {
  align-items: center;
  display: flex;
  flex: 1 1;
  font-family: "Poppins", sans-serif;
}
.item-right-side {
  margin-left: 15px;
  display: flex;
  align-items: center;
  font-family: "Poppins", sans-serif;
}
.item-right-side a {
  color: hsl(180, 66%, 49%);
}
#button.shortened-button {
  cursor: pointer;
  margin-left: 20px;
}

.statistics {
  font-family: "Poppins", sans-serif;
  margin-bottom: 50px;
}
.statistics-top-text {
  text-align: center;
  color: hsl(0, 0%, 25%);
}
.statistics-desc {
  display: flex;
  justify-content: center;
  color: hsl(0, 0%, 40%);
}
.statistics-desc p {
  max-width: 500px;
}
.statistics-cards {
  margin-top: 60px;
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  position: relative;
}
.statistics-cards::after {
  content: "";
  position: absolute;
  width: 70%;
  height: 6px;
  top: 110px;
  left: 20px;
  z-index: -1;
  background: hsl(180, 66%, 49%);
}
.statistics-card {
  background: rgb(243, 243, 243);
  box-shadow: 0px 5px 7px 0.1px rgba(218, 218, 218, 0.8);
  padding: 20px;
  margin: 15px;
  transition: all 0.35s;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  border-radius: 10px;
  cursor: pointer;
  transform: translateY(20px);
}
.statistics-card:hover {
  transform: scale(1.02);
  transform: scale(1.02);
  box-shadow: 0px 5px 7px 0.1px rgb(208, 208, 208);
}
.statistics-card-img img {
  background: hsl(255, 11%, 22%);
  padding: 10px;
  border-radius: 100%;
}
.statistics-card-title {
  margin-top: 10px;
  font-size: 1.3rem;
}
.statistics-card-desc {
  margin-top: 5px;
  font-size: 0.8rem;
}

